import { Box, BoxProps, SxProps } from "@mui/material";
import { BaseProps } from "@mui/material/OverridableComponent";

interface Props {
  readonly src: string;
  readonly style?: SxProps;
  readonly onClick?: () => void;
  readonly props?: BoxProps;
}

const Image = ({ src, onClick, props, style }: Props) =>
  <Box
    {...props}
    loading="lazy"
    referrerPolicy="no-referrer"
    onClick={onClick}
    component="img"
    src={src}
    sx={style} />

export default Image;